import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import MUIDataTable from "mui-datatables";
import axios from "../../axios";
import { Button } from "@material-ui/core";
import { MDBBtn, MDBModal, MDBModalBody, MDBModalFooter } from "mdbreact";
export default function StudentList() {
  const Dept_ID = useSelector((state) => state.Dept_ID);
   const collegeID = useSelector((state) => state.collegeID);
  const [data, setdata] = useState([]);
  const [flag, setflag] = useState("");
  const [modal, setModal] = useState(false);
  const [del, setDel] = useState(false);
  const [noticeID, setnoticeID] = useState("");
  const toggle = () => {
    setModal(!modal);
  };
  function deleteconfirm(x, callback) {
    if (x === 1) setModal(!modal);
    callback(x);
  }
  function delcallback(x) {
    if (x === 1) {
      const url = `/notifications/noticedelete/` + noticeID;

      axios
        .delete(url)
        .then((res) => {
          if (res.data.flag === "success") {
            alert("The Notification deleted Successfully");

            setflag("1");
          }
        })
        .catch((err) => {
          alert(err);
        });
      setDel(false);
    }
  }
  const columns = [
    {
      name: "ID",
      options: {
        filter: false,
        sort: false,
        display: false,
      },
    },
    {
      name: "Title",
      options: {
        filter: false,
        sort: false,
        display: true,
      },
    },
    {
      name: "Publication Date",
      options: {
        filter: false,
        sort: true,
        display: true,
      },
    },
    {
      name: "Category",
      options: {
        filter: false,
        sort: false,
        display: true,
      },
    },
    {
      name: "Delete",
      options: {
        filter: true,
        sort: false,
        empty: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <Button
              variant="contained"
              color="secondary"
              onClick={() => {
                toggle();
                setnoticeID(tableMeta.rowData[0]);
              }}
            >
              Delete
            </Button>
          );
        },
      },
    },
  ];

  const options = {
    filter: false,
    filterType: "dropdown",
    responsive: "scroll",
    tableBodyHeight: "100%",
    tableBodyMaxHeight: "standard",
    print: false,
    elevation: 8,
    searchPlaceholder: "Search Notification",
    draggableColumns: { enabled: true },
    rowsPerPageOptions: [10, 20, 50, 100],
    downloadOptions: {
      filename: "ascent.csv",
      separator: ",",
      filterOptions: {
        useDisplayedColumnsOnly: true,
        useDisplayedRowsOnly: true,
      },
    },
  };

  useEffect(() => {
    let cat_type;
    if (Dept_ID === 101) {
      cat_type = "exam";
    } else if (Dept_ID === 102) {
      cat_type = "admission";
    } else if (Dept_ID === 100) {
      cat_type = "all";
    }
   
    axios
      .get(`notifications/notification/` + cat_type + "/" + collegeID, {})
      .then((res) => {
        var persons = [];
        let array = res.data;
        for (let i in array) {
          var temp = [];
          temp.push(array[i].id);
          temp.push(array[i].title);
          temp.push(array[i].pubdate);
          temp.push(array[i].category);
          persons.push(temp);
        }
        setdata(persons);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [flag]);

  return (
    <React.Fragment>
      <MUIDataTable
        title={"List Of  Notifications"}
        data={data}
        columns={columns}
        options={options}
      />
      <MDBModal isOpen={modal} toggle={toggle}>
        <MDBModalBody>Are you sure you want to delete?</MDBModalBody>
        <MDBModalFooter>
          <MDBBtn color="danger" onClick={() => deleteconfirm(1, delcallback)}>
            Delete
          </MDBBtn>
          <MDBBtn color="primary" onClick={toggle}>
            Close
          </MDBBtn>
        </MDBModalFooter>
      </MDBModal>
    </React.Fragment>
  );
}
