import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Listnotification from './Listnotification'
class index extends Component {
    render() {
        return (
        <Grid container spacing={3}>
        <Grid item xs={12}>
         <Listnotification/>
        </Grid>
       
      </Grid>
        );
    }
}

export default index;
